
.mt-3 {
  margin-top: 20px;
}
.main-loader {
  border: 4px solid #ccc;
  border-top-color: #aaa;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  -webkit-animation: rotate-main-loader 1s linear infinite;
          animation: rotate-main-loader 1s linear infinite;
}
@-webkit-keyframes rotate-main-loader {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotate-main-loader {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}

.search-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.loader {
  position: relative;
  visibility: hidden;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border: 3px solid #ccc;
  border-top-color: #aaa;
  border-radius: 100%;
  width: 14px;
  height: 14px;
}
.loader-active {
  visibility: visible;
  -webkit-animation: rotate-loader .5s linear infinite;
          animation: rotate-loader .5s linear infinite;
}
.search-control-alert {
  position: absolute;
  top: 100%;
}
.conjugate-btn:hover .incline-animate {
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
}
.incline-animate {
  display: inline-block;
  -webkit-transition: all .2s linear;
  transition: all .2s linear;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
}
@-webkit-keyframes rotate-loader {
0% {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
100% {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
}
}
@keyframes rotate-loader {
0% {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
100% {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
}
}

.suggestions {
  position: absolute;
  z-index: 999;
  top: 100%;
  width: 100%;
}

.capitalize {
  text-transform: capitalize;
}

